
import type { Colaborador } from '@/types/payroll';

// Mock database of collaborators
const mockCollaborators: Colaborador[] = [
  { id_colaborador: 'colab-001', nombre: 'Juan Pérez', salarioBaseQuincenal: 711750 },
  { id_colaborador: 'colab-002', nombre: 'María García', salarioBaseQuincenal: 750000 },
  { id_colaborador: 'colab-003', nombre: 'Carlos Rodríguez', salarioBaseQuincenal: 680000 },
  // Add more collaborators as needed
];

/**
 * Fetches a list of all collaborators.
 * In a real application, this would fetch from a database or API.
 * @returns A promise that resolves to an array of Colaborador objects.
 */
export async function getCollaborators(): Promise<Colaborador[]> {
  // Simulate API delay
  await new Promise(resolve => setTimeout(resolve, 300));
  // In a real app: fetch('/api/collaborators')
  return mockCollaborators;
}

/**
 * Fetches a single collaborator by their ID.
 * @param id_colaborador The ID of the collaborator to fetch.
 * @returns A promise that resolves to the Colaborador object or undefined if not found.
 */
export async function getCollaboratorById(id_colaborador: string): Promise<Colaborador | undefined> {
   // Simulate API delay
  await new Promise(resolve => setTimeout(resolve, 100));
  const collaborator = mockCollaborators.find(c => c.id_colaborador === id_colaborador);
  // In a real app: fetch(`/api/collaborators/${id_colaborador}`)
  return collaborator;
}
