
'use client';

import React, { useState, useEffect, useTransition } from 'react';
import { zodResolver } from '@hookform/resolvers/zod';
import { useForm } from 'react-hook-form';
import { z } from 'zod';
import { format, startOfMonth, endOfMonth, subDays, addDays, parseISO, isValid as isDateValid } from 'date-fns';
import { es } from 'date-fns/locale';
import { v4 as uuidv4 } from 'uuid'; // For generating unique IDs for deductions/income

import { Button } from '@/components/ui/button';
import {
    Form, FormControl, FormField, FormItem, FormLabel, FormMessage, FormDescription
} from '@/components/ui/form';
import {
    Select, SelectContent, SelectItem, SelectTrigger, SelectValue,
} from '@/components/ui/select';
import { Calendar } from '@/components/ui/calendar';
import {
    Popover, PopoverContent, PopoverTrigger,
} from '@/components/ui/popover';
import {
    Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter, DialogTrigger, DialogClose
} from "@/components/ui/dialog";
import { Input } from '@/components/ui/input';
import { Switch } from '@/components/ui/switch';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from "@/components/ui/card";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Separator } from "@/components/ui/separator";
import { useToast } from '@/hooks/use-toast';
import { CalendarIcon, Loader2, AlertCircle, CheckCircle2, PlusCircle, Trash2, Edit2 } from 'lucide-react';
import { cn } from '@/lib/utils';

import type { Colaborador, Turno, PayrollCalculationInput, PayrollCalculationResult, PayrollError, NominaCalculada, OtraDeduccion, OtroIngreso } from '@/types/payroll';
import { getCollaborators, getCollaboratorById } from '@/services/collaborators'; // Mock service
import { getShiftsForCollaborator } from '@/services/shifts'; // Mock service
import { calculateBiWeeklyPayroll } from '@/actions/calculate-payroll'; // Server action
import { saveCalculatedPayroll } from '@/services/payroll'; // Mock service

import { PayrollResultsDisplay } from './payroll-results-display'; // Assuming this component exists or will be created

// --- Zod Schema for Form Validation ---
const payrollFormSchema = z.object({
  id_colaborador: z.string().min(1, { message: "Selecciona un colaborador." }),
  fecha_inicio: z.date({ required_error: "Fecha de inicio requerida." }),
  fecha_fin: z.date({ required_error: "Fecha de fin requerida." }),
  aplicarAuxilioTransporte: z.boolean().default(true), // Default to true, user can toggle
});

type PayrollFormValues = z.infer<typeof payrollFormSchema>;

// --- Zod Schema for Other Deduction/Income Dialog ---
const otherAdjustmentSchema = z.object({
    descripcion: z.string().min(1, "La descripción es requerida."),
    valor: z.number().min(0, "El valor debe ser positivo."),
});
type OtherAdjustmentValues = z.infer<typeof otherAdjustmentSchema>;


export function PayrollCalculator() {
  const { toast } = useToast();
  const [isPending, startTransition] = useTransition();
  const [isSaving, setIsSaving] = useState(false);
  const [collaborators, setCollaborators] = useState<Colaborador[]>([]);
  const [selectedCollaborator, setSelectedCollaborator] = useState<Colaborador | null>(null);
  const [calculationResult, setCalculationResult] = useState<PayrollCalculationResult | null>(null);
  const [calculationError, setCalculationError] = useState<string | null>(null);

  // State for managing other deductions and income
  const [otrasDeducciones, setOtrasDeducciones] = useState<OtraDeduccion[]>([]);
  const [otrosIngresos, setOtrosIngresos] = useState<OtroIngreso[]>([]);
  const [isAdjustmentDialogOpen, setIsAdjustmentDialogOpen] = useState(false);
  const [adjustmentType, setAdjustmentType] = useState<'deduccion' | 'ingreso'>('deduccion');
  const [editingAdjustment, setEditingAdjustment] = useState<OtraDeduccion | OtroIngreso | null>(null);


  const form = useForm<PayrollFormValues>({
    resolver: zodResolver(payrollFormSchema),
    defaultValues: {
      id_colaborador: '',
      fecha_inicio: undefined, // Will be set based on period selection
      fecha_fin: undefined,
      aplicarAuxilioTransporte: true,
    },
  });

  // --- Fetch Collaborators on Mount ---
  useEffect(() => {
    async function fetchCollaborators() {
      try {
        const data = await getCollaborators();
        setCollaborators(data);
      } catch (error) {
        toast({ title: "Error", description: "No se pudieron cargar los colaboradores.", variant: "destructive" });
      }
    }
    fetchCollaborators();
  }, [toast]);

   // --- Effect to set default dates (e.g., first half of current month) ---
   useEffect(() => {
     const today = new Date();
     const firstDayOfMonth = startOfMonth(today);
     const fifteenthDay = addDays(firstDayOfMonth, 14); // Day 15
     // Default to first quincena
     form.setValue('fecha_inicio', firstDayOfMonth);
     form.setValue('fecha_fin', fifteenthDay);
   }, [form]);

  // --- Handle Collaborator Selection Change ---
  const handleCollaboratorChange = async (id_colaborador: string) => {
    form.setValue('id_colaborador', id_colaborador);
    if (id_colaborador) {
      const collaborator = collaborators.find(c => c.id_colaborador === id_colaborador);
      setSelectedCollaborator(collaborator || null);
      resetCalculationState(); // Reset results when collaborator changes
    } else {
      setSelectedCollaborator(null);
      resetCalculationState();
    }
  };

  // --- Define Period Handlers (e.g., First/Second Quincena) ---
  const setPeriod = (type: 'first' | 'second') => {
    const currentStartDate = form.getValues('fecha_inicio');
    if (!currentStartDate || !isDateValid(currentStartDate)) return;

    const monthStart = startOfMonth(currentStartDate);
    const fifteenth = addDays(monthStart, 14);
    const monthEnd = endOfMonth(currentStartDate);

    if (type === 'first') {
        form.setValue('fecha_inicio', monthStart);
        form.setValue('fecha_fin', fifteenth);
    } else { // second
        form.setValue('fecha_inicio', addDays(fifteenth, 1)); // Start on the 16th
        form.setValue('fecha_fin', monthEnd);
    }
     resetCalculationState(); // Reset results when period changes
  };


  // --- Reset Calculation State ---
   const resetCalculationState = () => {
       setCalculationResult(null);
       setCalculationError(null);
       setOtrasDeducciones([]); // Also clear adjustments
       setOtrosIngresos([]);
       // Keep form values like collaborator and dates
   };

   // --- Clear Full Form and State ---
    const clearAll = () => {
        form.reset({
             id_colaborador: '',
             fecha_inicio: startOfMonth(new Date()),
             fecha_fin: addDays(startOfMonth(new Date()), 14),
             aplicarAuxilioTransporte: true,
        });
        setSelectedCollaborator(null);
        resetCalculationState();
        toast({ title: "Formulario Limpio", description: "Se han reiniciado todos los campos."});
    };


  // --- Handle Payroll Calculation ---
  const handleCalculate = async (values: PayrollFormValues) => {
    setCalculationError(null);
    setCalculationResult(null); // Clear previous results

    if (!selectedCollaborator) {
      setCalculationError("Selecciona un colaborador primero.");
      return;
    }

    startTransition(async () => {
      try {
        // 1. Fetch Shifts
        const turnos = await getShiftsForCollaborator(
          values.id_colaborador,
          values.fecha_inicio,
          values.fecha_fin
        );

        // Prepare input for the server action
        const calculationInput: PayrollCalculationInput = {
          id_colaborador: values.id_colaborador,
          fecha_inicio: values.fecha_inicio,
          fecha_fin: values.fecha_fin,
          turnos: turnos,
          salarioBaseQuincenal: selectedCollaborator.salarioBaseQuincenal,
          aplicarAuxilioTransporte: values.aplicarAuxilioTransporte,
          otrasDeducciones: otrasDeducciones, // Pass current deductions
          otrosIngresos: otrosIngresos, // Pass current income
        };

        // 2. Call Server Action
        const result = await calculateBiWeeklyPayroll(calculationInput);

        if ('error' in result) {
          setCalculationError(result.error);
          toast({ title: "Error de Cálculo", description: result.error, variant: "destructive" });
        } else {
          setCalculationResult(result);
          // Update deductions/income lists in the result in case they were modified during calculation (though usually passed as input)
          setOtrasDeducciones(result.otrasDeduccionesDetalle || []);
          // Assuming OtrosIngresos might also be returned if modified by backend logic (less common)
          // setOtrosIngresos(result.otrosIngresosDetalle || []);
          toast({ title: "Cálculo Exitoso", description: `Nómina para ${selectedCollaborator.nombre} calculada.`, variant: "default" });
        }
      } catch (error) {
        console.error("Payroll calculation failed:", error);
        const message = error instanceof Error ? error.message : "Ocurrió un error inesperado.";
        setCalculationError(`Error al calcular: ${message}`);
        toast({ title: "Error Crítico", description: `Fallo en la comunicación con el servidor: ${message}`, variant: "destructive" });
      }
    });
  };

   // --- Handle Adding/Editing Other Adjustments (Deductions/Income) ---
   const openAdjustmentDialog = (type: 'deduccion' | 'ingreso', item?: OtraDeduccion | OtroIngreso) => {
       setAdjustmentType(type);
       setEditingAdjustment(item || null); // Set item if editing, null if adding
       setIsAdjustmentDialogOpen(true);
   };

   const handleSaveAdjustment = (values: OtherAdjustmentValues) => {
       const newAdjustment = { ...values, id: editingAdjustment?.id || uuidv4() };

       if (adjustmentType === 'deduccion') {
           setOtrasDeducciones(prev =>
                editingAdjustment
                ? prev.map(d => d.id === editingAdjustment.id ? newAdjustment as OtraDeduccion : d)
                : [...prev, newAdjustment as OtraDeduccion]
           );
       } else { // ingreso
           setOtrosIngresos(prev =>
                editingAdjustment
                ? prev.map(i => i.id === editingAdjustment.id ? newAdjustment as OtroIngreso : i)
                : [...prev, newAdjustment as OtroIngreso]
            );
       }

       setIsAdjustmentDialogOpen(false);
       setEditingAdjustment(null);
        // Optional: Recalculate immediately after adding/editing adjustment
        // This requires re-fetching shifts and calling the calculation action again
        // Consider if this is desired or if recalculation happens only via the main button
        // form.handleSubmit(handleCalculate)(); // Trigger recalculation
       toast({
           title: `${editingAdjustment ? 'Ajuste Actualizado' : 'Ajuste Añadido'}`,
           description: `${adjustmentType === 'deduccion' ? 'Deducción' : 'Ingreso'} "${values.descripcion}" ${editingAdjustment ? 'actualizado' : 'añadido'}.`,
       });
       // Manually trigger recalculation if needed after state update
        handleRecalculateWithAdjustments();

   };

   const handleDeleteAdjustment = (type: 'deduccion' | 'ingreso', id: string) => {
       if (type === 'deduccion') {
           setOtrasDeducciones(prev => prev.filter(d => d.id !== id));
           toast({ title: "Deducción Eliminada", variant: "destructive"});
       } else {
           setOtrosIngresos(prev => prev.filter(i => i.id !== id));
           toast({ title: "Ingreso Eliminado", variant: "destructive"});
       }
       // Manually trigger recalculation if needed after state update
        handleRecalculateWithAdjustments();
   };

   // Function to re-run calculation with updated adjustments
   const handleRecalculateWithAdjustments = () => {
       // Ensure form values are valid before triggering
       form.trigger().then(isValid => {
            if (isValid) {
                 handleCalculate(form.getValues());
            } else {
                toast({ title: "Error", description: "Hay errores en el formulario principal.", variant: "destructive" });
            }
       });
   };


  // --- Handle Saving the Calculated Payroll ---
  const handleSavePayroll = async () => {
    if (!calculationResult) {
      toast({ title: "Error", description: "No hay resultados de nómina para guardar.", variant: "destructive" });
      return;
    }
    if (isPending || isSaving) return; // Prevent double saving

    setIsSaving(true);
    try {
      // The calculationResult already contains all necessary data for NominaCalculada
      const nominaToSave: NominaCalculada = {
          ...calculationResult,
          // Ensure details are included if needed by backend
          otrasDeduccionesDetalle: otrasDeducciones,
          // otrosIngresosDetalle: otrosIngresos, // Assuming backend doesn't need this if total is present
        };

      const saveResult = await saveCalculatedPayroll(nominaToSave);

      if ('error' in saveResult) {
        toast({ title: "Error al Guardar", description: saveResult.error, variant: "destructive" });
      } else {
        toast({ title: "Nómina Guardada", description: `Nómina para ${selectedCollaborator?.nombre} guardada exitosamente (ID: ${saveResult.id}).`, variant: "default", className: "bg-green-100 dark:bg-green-900" });
        // --- Phase 4: Preparation for Next Collaborator ---
        clearAll(); // Clear form and results for the next one
      }
    } catch (error) {
      console.error("Failed to save payroll:", error);
      const message = error instanceof Error ? error.message : "Error de conexión o servidor.";
      toast({ title: "Error Crítico al Guardar", description: message, variant: "destructive" });
    } finally {
      setIsSaving(false);
    }
  };


  return (
    <div className="container mx-auto p-4 md:p-8 space-y-8">
      <h1 className="text-3xl font-bold text-center text-primary">Calculadora de Nómina Quincenal</h1>

      {/* --- Phase 1: Selection and Data Loading --- */}
      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle>1. Seleccionar Colaborador y Periodo</CardTitle>
          <CardDescription>Elige el colaborador y el rango de fechas para calcular la nómina.</CardDescription>
        </CardHeader>
        <CardContent>
          <Form {...form}>
            <form onSubmit={form.handleSubmit(handleCalculate)} className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4 items-end">
                {/* Collaborator Selection */}
                <FormField
                  control={form.control}
                  name="id_colaborador"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Colaborador</FormLabel>
                      <Select onValueChange={handleCollaboratorChange} value={field.value}>
                        <FormControl>
                          <SelectTrigger>
                            <SelectValue placeholder="Selecciona un colaborador..." />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent>
                          {collaborators.map(c => (
                            <SelectItem key={c.id_colaborador} value={c.id_colaborador}>
                              {c.nombre} ({c.id_colaborador})
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                 {/* Period Definition */}
                 <div className="grid grid-cols-2 gap-2">
                     <FormField
                       control={form.control}
                       name="fecha_inicio"
                       render={({ field }) => (
                         <FormItem className="flex flex-col">
                           <FormLabel>Fecha Inicio</FormLabel>
                            <Popover>
                             <PopoverTrigger asChild>
                               <FormControl>
                                 <Button
                                   variant={'outline'}
                                   className={cn("pl-3 text-left font-normal", !field.value && "text-muted-foreground")}
                                 >
                                   {field.value ? format(field.value, 'dd/MM/yy') : <span>Fecha</span>}
                                   <CalendarIcon className="ml-auto h-4 w-4 opacity-50" />
                                 </Button>
                               </FormControl>
                             </PopoverTrigger>
                             <PopoverContent className="w-auto p-0" align="start">
                               <Calendar mode="single" selected={field.value} onSelect={(date) => { field.onChange(date); resetCalculationState(); }} locale={es} initialFocus />
                             </PopoverContent>
                           </Popover>
                           <FormMessage />
                         </FormItem>
                       )}
                     />
                     <FormField
                       control={form.control}
                       name="fecha_fin"
                       render={({ field }) => (
                         <FormItem className="flex flex-col">
                           <FormLabel>Fecha Fin</FormLabel>
                            <Popover>
                             <PopoverTrigger asChild>
                               <FormControl>
                                 <Button
                                   variant={'outline'}
                                   className={cn("pl-3 text-left font-normal", !field.value && "text-muted-foreground")}
                                 >
                                   {field.value ? format(field.value, 'dd/MM/yy') : <span>Fecha</span>}
                                   <CalendarIcon className="ml-auto h-4 w-4 opacity-50" />
                                 </Button>
                               </FormControl>
                             </PopoverTrigger>
                             <PopoverContent className="w-auto p-0" align="start">
                               <Calendar mode="single" selected={field.value} onSelect={(date) => { field.onChange(date); resetCalculationState(); }} locale={es} initialFocus />
                             </PopoverContent>
                           </Popover>
                           <FormMessage />
                         </FormItem>
                       )}
                     />
                 </div>

                 {/* Quick Period Buttons & Calculate Button */}
                 <div className="flex flex-col space-y-2 md:flex-row md:space-y-0 md:space-x-2 md:items-end">
                    <div className="flex space-x-2">
                         <Button type="button" variant="secondary" size="sm" onClick={() => setPeriod('first')}>1ª Q.</Button>
                         <Button type="button" variant="secondary" size="sm" onClick={() => setPeriod('second')}>2ª Q.</Button>
                    </div>
                    <Button type="submit" className="flex-grow bg-accent hover:bg-accent/90 text-accent-foreground" disabled={isPending || !selectedCollaborator}>
                        {isPending ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                        Calcular Nómina
                     </Button>
                 </div>

              </div>

                {/* Auxilio de Transporte Switch */}
               <FormField
                 control={form.control}
                 name="aplicarAuxilioTransporte"
                 render={({ field }) => (
                   <FormItem className="flex flex-row items-center justify-between rounded-lg border p-3 shadow-sm bg-secondary/50 max-w-md">
                     <div className="space-y-0.5">
                       <FormLabel>Aplicar Auxilio de Transporte</FormLabel>
                       <FormDescription>
                         Se aplicará si el colaborador cumple los requisitos legales.
                       </FormDescription>
                     </div>
                     <FormControl>
                       <Switch
                         checked={field.value}
                         onCheckedChange={(checked) => {
                           field.onChange(checked);
                           // Optionally trigger recalculation immediately
                           handleRecalculateWithAdjustments();
                         }}
                         disabled={isPending || !selectedCollaborator} // Disable if no collaborator or calculating
                       />
                     </FormControl>
                   </FormItem>
                 )}
               />
            </form>
          </Form>
        </CardContent>
      </Card>

      {/* --- Phase 2 & 3: Calculation Results and Actions --- */}
       { (calculationResult || calculationError || isPending) && (
         <Card className="shadow-lg">
             <CardHeader>
                 <CardTitle>2. Resultados del Cálculo</CardTitle>
                 {isPending && <CardDescription>Calculando nómina...</CardDescription>}
                 {calculationError && !isPending && <CardDescription className="text-destructive">Error en el cálculo.</CardDescription>}
                 {calculationResult && !isPending && <CardDescription>Revisa los detalles de la nómina calculada.</CardDescription>}
             </CardHeader>
             <CardContent>
                  {isPending && (
                       <div className="flex justify-center items-center py-8">
                           <Loader2 className="h-8 w-8 animate-spin text-primary" />
                           <span className="ml-3 text-muted-foreground">Cargando y calculando...</span>
                       </div>
                  )}
                 {calculationError && !isPending && (
                     <Alert variant="destructive">
                       <AlertCircle className="h-4 w-4" />
                       <AlertTitle>Error</AlertTitle>
                       <AlertDescription>{calculationError}</AlertDescription>
                     </Alert>
                 )}
                 {calculationResult && !isPending && (
                     <>
                         {/* Display Main Calculation Results */}
                         <PayrollResultsDisplay result={calculationResult} />

                         <Separator className="my-6" />

                          {/* Other Deductions Section */}
                         <div className="space-y-4">
                             <div className="flex justify-between items-center">
                                 <h3 className="text-lg font-semibold text-primary">Otras Deducciones</h3>
                                 <Button variant="outline" size="sm" onClick={() => openAdjustmentDialog('deduccion')}>
                                     <PlusCircle className="mr-2 h-4 w-4" /> Añadir Deducción
                                 </Button>
                             </div>
                             {otrasDeducciones.length > 0 ? (
                                <ScrollArea className="h-32">
                                 <Table>
                                     <TableHeader>
                                         <TableRow>
                                             <TableHead>Descripción</TableHead>
                                             <TableHead className="text-right">Valor</TableHead>
                                             <TableHead className="text-right">Acciones</TableHead>
                                         </TableRow>
                                     </TableHeader>
                                     <TableBody>
                                         {otrasDeducciones.map(d => (
                                             <TableRow key={d.id}>
                                                 <TableCell>{d.descripcion}</TableCell>
                                                 <TableCell className="text-right">{formatCurrency(d.valor)}</TableCell>
                                                 <TableCell className="text-right space-x-1">
                                                      <Button variant="ghost" size="icon" className="h-6 w-6" onClick={() => openAdjustmentDialog('deduccion', d)}>
                                                         <Edit2 className="h-4 w-4" />
                                                     </Button>
                                                     <Button variant="ghost" size="icon" className="h-6 w-6 text-destructive hover:text-destructive" onClick={() => handleDeleteAdjustment('deduccion', d.id)}>
                                                         <Trash2 className="h-4 w-4" />
                                                     </Button>
                                                 </TableCell>
                                             </TableRow>
                                         ))}
                                     </TableBody>
                                 </Table>
                                 </ScrollArea>
                             ) : (
                                 <p className="text-sm text-muted-foreground text-center py-4">No hay otras deducciones añadidas.</p>
                             )}
                         </div>

                         <Separator className="my-6" />


                          {/* Other Income Section */}
                          <div className="space-y-4">
                             <div className="flex justify-between items-center">
                                 <h3 className="text-lg font-semibold text-primary">Otros Ingresos (No Salariales)</h3>
                                  <Button variant="outline" size="sm" onClick={() => openAdjustmentDialog('ingreso')}>
                                     <PlusCircle className="mr-2 h-4 w-4" /> Añadir Ingreso
                                 </Button>
                             </div>
                             {otrosIngresos.length > 0 ? (
                                <ScrollArea className="h-32">
                                 <Table>
                                     <TableHeader>
                                         <TableRow>
                                             <TableHead>Descripción</TableHead>
                                             <TableHead className="text-right">Valor</TableHead>
                                             <TableHead className="text-right">Acciones</TableHead>
                                         </TableRow>
                                     </TableHeader>
                                     <TableBody>
                                         {otrosIngresos.map(i => (
                                             <TableRow key={i.id}>
                                                 <TableCell>{i.descripcion}</TableCell>
                                                 <TableCell className="text-right">{formatCurrency(i.valor)}</TableCell>
                                                  <TableCell className="text-right space-x-1">
                                                      <Button variant="ghost" size="icon" className="h-6 w-6" onClick={() => openAdjustmentDialog('ingreso', i)}>
                                                         <Edit2 className="h-4 w-4" />
                                                     </Button>
                                                     <Button variant="ghost" size="icon" className="h-6 w-6 text-destructive hover:text-destructive" onClick={() => handleDeleteAdjustment('ingreso', i.id)}>
                                                         <Trash2 className="h-4 w-4" />
                                                     </Button>
                                                 </TableCell>
                                             </TableRow>
                                         ))}
                                     </TableBody>
                                 </Table>
                                 </ScrollArea>
                             ) : (
                                 <p className="text-sm text-muted-foreground text-center py-4">No hay otros ingresos añadidos.</p>
                             )}
                         </div>

                         <Separator className="my-6" />

                         {/* Final Net Pay Display - Extracted to PayrollResultsDisplay */}
                         {/* <div className="text-right mt-6">
                              <p className="text-lg font-semibold">Total Otras Deducciones: <span className="text-destructive">{formatCurrency(calculationResult.totalOtrasDeducciones)}</span></p>
                              <p className="text-xl font-bold text-primary mt-2">Neto a Pagar Final: {formatCurrency(calculationResult.netoAPagarQuincenal)}</p>
                         </div> */}
                     </>
                 )}
             </CardContent>
              {calculationResult && !isPending && (
                <CardFooter className="flex justify-end space-x-2">
                     <Button variant="outline" onClick={clearAll} disabled={isSaving}>Limpiar Todo</Button>
                     <Button onClick={handleSavePayroll} disabled={isPending || isSaving} className="bg-green-600 hover:bg-green-700 text-white">
                         {isSaving ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <CheckCircle2 className="mr-2 h-4 w-4" />}
                         Guardar Nómina
                     </Button>
                </CardFooter>
             )}
         </Card>
        )}


        {/* --- Dialog for Adding/Editing Other Adjustments --- */}
      <Dialog open={isAdjustmentDialogOpen} onOpenChange={setIsAdjustmentDialogOpen}>
          <DialogContent>
              <DialogHeader>
                  <DialogTitle>{editingAdjustment ? 'Editar' : 'Añadir'} {adjustmentType === 'deduccion' ? 'Deducción' : 'Ingreso'}</DialogTitle>
                  <DialogDescription>
                      Ingresa los detalles {adjustmentType === 'deduccion' ? 'de la deducción' : 'del ingreso no salarial'}.
                  </DialogDescription>
              </DialogHeader>
               {/* Simple form inside dialog - consider using react-hook-form here too for robust validation */}
               <AdjustmentForm
                    initialData={editingAdjustment}
                    onSubmit={handleSaveAdjustment}
                    onCancel={() => setIsAdjustmentDialogOpen(false)}
               />
          </DialogContent>
      </Dialog>

    </div>
  );
}


// --- Helper Component for Adjustment Form ---
interface AdjustmentFormProps {
    initialData: OtraDeduccion | OtroIngreso | null;
    onSubmit: (values: OtherAdjustmentValues) => void;
    onCancel: () => void;
}

const AdjustmentForm: React.FC<AdjustmentFormProps> = ({ initialData, onSubmit, onCancel }) => {
     const adjForm = useForm<OtherAdjustmentValues>({
         resolver: zodResolver(otherAdjustmentSchema),
         defaultValues: {
             descripcion: initialData?.descripcion || '',
             valor: initialData?.valor || 0,
         },
     });

     const handleSubmit = (values: OtherAdjustmentValues) => {
         onSubmit(values);
         adjForm.reset(); // Reset form after submission
     };

    return (
         <Form {...adjForm}>
             <form onSubmit={adjForm.handleSubmit(handleSubmit)} className="space-y-4 py-4">
                 <FormField
                     control={adjForm.control}
                     name="descripcion"
                     render={({ field }) => (
                         <FormItem>
                             <FormLabel>Descripción</FormLabel>
                             <FormControl>
                                 <Input placeholder="Ej: Préstamo, Bonificación" {...field} />
                             </FormControl>
                             <FormMessage />
                         </FormItem>
                     )}
                 />
                 <FormField
                     control={adjForm.control}
                     name="valor"
                      render={({ field }) => (
                         <FormItem>
                             <FormLabel>Valor ($)</FormLabel>
                             <FormControl>
                                 {/* Use type="number" and handle potential string value */}
                                 <Input
                                    type="number"
                                    placeholder="0"
                                    {...field}
                                    onChange={(e) => field.onChange(parseFloat(e.target.value) || 0)} // Ensure value is number
                                    value={field.value || ''} // Handle potential undefined/NaN
                                    min="0"
                                    step="any" // Allow decimals if needed
                                />
                             </FormControl>
                             <FormMessage />
                         </FormItem>
                     )}
                 />
                  <DialogFooter>
                     <Button type="button" variant="outline" onClick={onCancel}>Cancelar</Button>
                     <Button type="submit">Guardar Ajuste</Button>
                 </DialogFooter>
             </form>
         </Form>
     );
};


// --- Helper Functions ---
const formatCurrency = (value: number | undefined | null): string => {
    if (value === undefined || value === null) return '$ 0';
    return new Intl.NumberFormat('es-CO', {
        style: 'currency', currency: 'COP', minimumFractionDigits: 0, maximumFractionDigits: 0,
    }).format(value);
};
