
'use client';

import type { FC } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Separator } from '@/components/ui/separator';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion";
import type { PayrollCalculationResult } from '@/types/payroll';
import { Info, TrendingUp, TrendingDown, HandCoins, PiggyBank, Landmark, CircleDollarSign } from 'lucide-react';

interface PayrollResultsDisplayProps {
  result: PayrollCalculationResult;
}

// Mapeo de claves de horas agregadas a etiquetas legibles en español
const hoursLabelMap: Record<keyof PayrollCalculationResult['horasDetalladasAgregadas'], string> = {
    Ordinaria_Diurna_Base: 'Horas Base Diurnas (Umbral 7,66h)',
    Recargo_Noct_Base: 'Recargo Nocturno (Base)',
    Recargo_Dom_Diurno_Base: 'Recargo Dominical/Festivo Diurno (Base)',
    Recargo_Dom_Noct_Base: 'Recargo Dominical/Festivo Nocturno (Base)',
    HED: 'Horas Extras Diurnas (Laboral)',
    HEN: 'Horas Extras Nocturnas (Laboral)',
    HEDD_F: 'Horas Extras Diurnas (Dom/Fest)',
    HEND_F: 'Horas Extras Nocturnas (Dom/Fest)',
    totalHorasTrabajadas: 'Total Horas Trabajadas',
};

// Orden deseado para mostrar las horas/pagos detallados
const displayOrder: (keyof PayrollCalculationResult['horasDetalladasAgregadas'])[] = [
    'Ordinaria_Diurna_Base',
    'Recargo_Noct_Base',
    'Recargo_Dom_Diurno_Base',
    'Recargo_Dom_Noct_Base',
    'HED',
    'HEN',
    'HEDD_F',
    'HEND_F',
];

export const PayrollResultsDisplay: FC<PayrollResultsDisplayProps> = ({ result }) => {
    const {
        salarioBaseQuincenal,
        totalExtrasRecargosQuincenal,
        subtotalDevengadoQuincenal,
        esElegibleAuxilioTransporte,
        aplicarAuxilioTransporte, // Need this from input to display intent vs applied
        auxilioTransporteAplicado,
        totalOtrosIngresos,
        totalDevengadoBrutoQuincenal,
        ibc_Final_Quincenal,
        deduccionSaludQuincenal,
        deduccionPensionQuincenal,
        totalOtrasDeducciones,
        netoAPagarQuincenal,
        horasDetalladasAgregadas,
        pagoDetalladoAgregado,
    } = result;

  return (
    <Accordion type="multiple" defaultValue={['devengado', 'deducciones', 'neto']} className="w-full space-y-4">

      {/* Devengado (Earnings) Section */}
      <AccordionItem value="devengado" className="border rounded-lg shadow-sm overflow-hidden">
        <AccordionTrigger className="bg-secondary/30 hover:bg-secondary/50 px-4 py-3 text-lg font-semibold text-primary data-[state=open]:border-b">
            <TrendingUp className="mr-2 h-5 w-5 text-green-600" /> Devengado (Ingresos)
        </AccordionTrigger>
        <AccordionContent className="px-4 py-4 space-y-3 text-sm">
             <InfoRow label="Salario Base Quincenal" value={formatCurrency(salarioBaseQuincenal)} icon={CircleDollarSign} />
             <InfoRow label="Total Extras y Recargos" value={formatCurrency(totalExtrasRecargosQuincenal)} icon={HandCoins} />
             <InfoRow label="Subtotal (Base + Extras/Recargos)" value={formatCurrency(subtotalDevengadoQuincenal)} isSubtotal />
              <Separator />
             <InfoRow label="Otros Ingresos (No Salariales)" value={formatCurrency(totalOtrosIngresos)} icon={TrendingUp} />
             <InfoRow
                label="Auxilio de Transporte"
                value={formatCurrency(auxilioTransporteAplicado)}
                icon={PiggyBank}
                tooltip={
                    !esElegibleAuxilioTransporte ? "No elegible (supera 2 SMLMV)" :
                    !aplicarAuxilioTransporte ? "Opción desactivada por el usuario" :
                    auxilioTransporteAplicado > 0 ? "Aplicado según normativa" : "No aplica"
                 }
              />
             <Separator />
              <InfoRow label="Total Devengado Bruto" value={formatCurrency(totalDevengadoBrutoQuincenal)} isTotal />
        </AccordionContent>
      </AccordionItem>

       {/* Deducciones Section */}
       <AccordionItem value="deducciones" className="border rounded-lg shadow-sm overflow-hidden">
         <AccordionTrigger className="bg-secondary/30 hover:bg-secondary/50 px-4 py-3 text-lg font-semibold text-primary data-[state=open]:border-b">
            <TrendingDown className="mr-2 h-5 w-5 text-red-600" /> Deducciones
         </AccordionTrigger>
         <AccordionContent className="px-4 py-4 space-y-3 text-sm">
             <InfoRow label="Ingreso Base de Cotización (IBC)" value={formatCurrency(ibc_Final_Quincenal)} icon={Info} isSubtotal />
             <Separator className="my-2"/>
             <InfoRow label="Aporte Salud (4%)" value={formatCurrency(deduccionSaludQuincenal)} icon={Landmark} />
             <InfoRow label="Aporte Pensión (4%)" value={formatCurrency(deduccionPensionQuincenal)} icon={Landmark} />
             <Separator className="my-2"/>
             <InfoRow label="Total Otras Deducciones" value={formatCurrency(totalOtrasDeducciones)} icon={TrendingDown} />
             {/* You could list individual 'otrasDeducciones' here if needed */}
         </AccordionContent>
       </AccordionItem>

        {/* Resumen Neto Section */}
        <AccordionItem value="neto" className="border rounded-lg shadow-sm overflow-hidden">
         <AccordionTrigger className="bg-primary/10 hover:bg-primary/20 px-4 py-3 text-lg font-semibold text-primary data-[state=open]:border-b">
            <CircleDollarSign className="mr-2 h-5 w-5 text-primary" /> Neto a Pagar
         </AccordionTrigger>
         <AccordionContent className="px-4 py-6 flex justify-center items-center">
             <p className="text-2xl font-bold text-center text-primary">
                 {formatCurrency(netoAPagarQuincenal)}
             </p>
         </AccordionContent>
       </AccordionItem>

       {/* Detalle Horas Section (Optional) */}
      <AccordionItem value="detalle-horas" className="border rounded-lg shadow-sm overflow-hidden">
        <AccordionTrigger className="bg-secondary/30 hover:bg-secondary/50 px-4 py-3 text-lg font-semibold text-primary data-[state=open]:border-b">
            <Info className="mr-2 h-5 w-5" /> Detalle de Horas y Pagos Calculados
        </AccordionTrigger>
        <AccordionContent className="px-0 py-2">
             <Table>
               <TableHeader>
                 <TableRow>
                   <TableHead className="w-[45%] pl-4">Categoría</TableHead>
                   <TableHead className="text-right">Horas</TableHead>
                   <TableHead className="text-right pr-4">Pago (Recargo/Extra)</TableHead>
                 </TableRow>
               </TableHeader>
               <TableBody>
                 {displayOrder.map((key) => {
                   const horas = horasDetalladasAgregadas[key];
                   const pago = pagoDetalladoAgregado[key as keyof typeof pagoDetalladoAgregado];
                   // Only render if there are hours or payment for this category
                   if (horas > 0 || (pago > 0 && key !== 'Ordinaria_Diurna_Base')) {
                     return (
                       <TableRow key={key}>
                         <TableCell className="font-medium text-muted-foreground pl-4">{hoursLabelMap[key] || key}</TableCell>
                         <TableCell className="text-right">{formatHours(horas)}</TableCell>
                         <TableCell className="text-right pr-4">{formatCurrency(pago)}</TableCell>
                       </TableRow>
                     );
                   }
                   return null;
                 })}
                  <TableRow className="bg-secondary font-semibold">
                       <TableCell className="pl-4">Total Horas Trabajadas</TableCell>
                       <TableCell className="text-right">{formatHours(horasDetalladasAgregadas.totalHorasTrabajadas)}</TableCell>
                       <TableCell className="text-right pr-4">-</TableCell>
                  </TableRow>
               </TableBody>
             </Table>
        </AccordionContent>
      </AccordionItem>

    </Accordion>
  );
};

// --- Helper Component for Info Rows ---
interface InfoRowProps {
    label: string;
    value: string | number;
    icon?: React.ElementType;
    isSubtotal?: boolean;
    isTotal?: boolean;
    tooltip?: string;
}

const InfoRow: FC<InfoRowProps> = ({ label, value, icon: Icon, isSubtotal, isTotal, tooltip }) => (
    <div className={`flex justify-between items-center ${isSubtotal ? 'text-muted-foreground' : ''} ${isTotal ? 'font-bold text-primary text-base' : ''}`}>
        <span className="flex items-center">
            {Icon && <Icon className={`mr-2 h-4 w-4 ${isTotal ? 'text-primary' : 'text-muted-foreground'}`} />}
            {label}
             {tooltip && <Info className="ml-1 h-3 w-3 text-muted-foreground cursor-help" title={tooltip} />}
        </span>
        <span>{value}</span>
    </div>
);


// --- Helper Formatting Functions ---
const formatCurrency = (value: number | undefined | null): string => {
  if (value === undefined || value === null) return '$ 0';
  return new Intl.NumberFormat('es-CO', {
    style: 'currency',
    currency: 'COP',
    minimumFractionDigits: 0,
    maximumFractionDigits: 0, // Changed to 0 for cleaner display, adjust if cents are needed
  }).format(value);
};

const formatHours = (hours: number | undefined | null): string => {
  if (hours === undefined || hours === null) return '0.00';
  return hours.toLocaleString('es-CO', {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  });
};
