
import type { NominaCalculada, PayrollError } from '@/types/payroll';

// Mock database for saved payrolls
const savedPayrolls: NominaCalculada[] = [];

/**
 * Saves the calculated payroll data.
 * In a real application, this would send data to a backend API endpoint.
 * @param nominaData The complete NominaCalculada object to save.
 * @returns A promise that resolves to the saved NominaCalculada (potentially with a DB ID) or a PayrollError.
 */
export async function saveCalculatedPayroll(
  nominaData: NominaCalculada
): Promise<NominaCalculada | PayrollError> {
  // Simulate API delay
  await new Promise(resolve => setTimeout(resolve, 700));

  // Basic validation example (in real app, backend does more thorough validation)
  if (!nominaData.id_colaborador || !nominaData.fecha_inicio || !nominaData.fecha_fin) {
    return { error: 'Faltan datos esenciales para guardar la nómina (colaborador, fechas).' };
  }

  // Simulate potential backend conflict (e.g., payroll already saved for this period/collaborator)
  const existing = savedPayrolls.find(p =>
      p.id_colaborador === nominaData.id_colaborador &&
      p.fecha_inicio.getTime() === nominaData.fecha_inicio.getTime() &&
      p.fecha_fin.getTime() === nominaData.fecha_fin.getTime()
  );

  if (existing) {
      // Option 1: Return error
      // return { error: `La nómina para ${nominaData.id_colaborador} en este período ya existe.` };
      // Option 2: Update existing (more complex, requires PUT/PATCH logic)
      console.warn(`Actualizando nómina existente para ${nominaData.id_colaborador} en ${nominaData.fecha_inicio.toLocaleDateString()}`);
      Object.assign(existing, nominaData); // Simple merge for mock
      existing.fechaCalculo = new Date();
      return existing;

  } else {
      // Simulate adding to database and assigning an ID
      const newPayrollRecord: NominaCalculada = {
        ...nominaData,
        id: `nomina-${Date.now()}-${Math.random().toString(16).substring(2, 8)}`, // Mock ID generation
        fechaCalculo: new Date(),
      };
      savedPayrolls.push(newPayrollRecord);
      console.log('Nómina guardada:', newPayrollRecord);
      return newPayrollRecord;
  }


  // In a real app:
  /*
  try {
    const response = await fetch('/api/nominas-calculadas', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(nominaData),
    });
    if (!response.ok) {
      const errorData = await response.json();
      return { error: errorData.message || 'Error del servidor al guardar la nómina.' };
    }
    const savedData: NominaCalculada = await response.json();
    return savedData;
  } catch (error) {
    console.error("Error saving payroll:", error);
    return { error: 'Error de red o conexión al guardar la nómina.' };
  }
  */
}

/**
 * Fetches previously saved payroll calculations (optional).
 * Useful for viewing history.
 * @returns A promise that resolves to an array of NominaCalculada objects.
 */
export async function getSavedPayrolls(): Promise<NominaCalculada[]> {
   // Simulate API delay
   await new Promise(resolve => setTimeout(resolve, 400));
   // In a real app: fetch('/api/nominas-calculadas')
   return [...savedPayrolls]; // Return a copy
}
