/**
 * Fetches shifts for a specific collaborator within a given date range.
 * In a real application, this would query a database or API.
 * @param id_colaborador The ID of the collaborator.
 * @param fecha_inicio The start date of the period.
 * @param fecha_fin The end date of the period.
 * @returns A promise that resolves to an array of Turno objects.
 */
export async function getShiftsForCollaborator(
  id_colaborador: string,
  fecha_inicio: Date,
  fecha_fin: Date
): Promise<Turno[]> {
  // Simulate API delay
  await new Promise(resolve => setTimeout(resolve, 500));

  const filteredShifts = mockShifts.filter(turno => {
    return (
      turno.id_colaborador === id_colaborador &&
      isWithinInterval(turno.fecha, { start: fecha_inicio, end: fecha_fin })
    );
  });

  // In a real app: fetch(`/api/shifts?collaboratorId=${id_colaborador}&startDate=${fecha_inicio.toISOString()}&endDate=${fecha_fin.toISOString()}`)
  return filteredShifts;
}
